@import "../../css/share.scss";
.history_detail {
  padding: 0 px(34);
  border-top: 1px solid #393939;
}

.row{
  color: #fff;
  display: flex;
  padding: px(25) 0;
  border-bottom: 1px solid #393939;

  div{
    line-height: 1.4;
    font-size: px(28);

    &:nth-of-type(1){
      flex-grow: 0;
      flex-shrink: 0;
      min-width: px(132);
      margin-right: px(20);
    }

    &:nth-of-type(2){
      flex-grow: 1;
      color: #c1c1c1;
      word-break: break-all;
    }

  }
}
