$px: 0.01rem;
@function px($n) {
    @return $n * $px;
}

@mixin overFlowLine2 {
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    overflow: hidden;
    text-overflow: ellipsis;
}

$space: px(30);
@mixin shadow {
    box-shadow: 0 0 15px rgba(0, 0, 0, 0.3);
}

@mixin ellipsis {
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
}

$imgUrl: "../../images/";
